<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">基金管理</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>基金管理（{{total}}）</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="goCreatnew">
                <span class="icon_add"></span>添加新基金
              </div>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="downloadFundFile">
                <span class="icon_download"></span>下载基金模版
              </div>
            </div>
          </div>
          <div class="table-group-tab">
            <div class="clear"></div>
            <div class="line"></div>
          </div>
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th>序号</th>
                  <th>基金名称</th>
                  <th>基金规模（万元）</th>
                  <th>管理人</th>
                  <th width='98'>已投项目（个）</th>
                  <th>成立时间</th>
                  <th>操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="index">
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td>{{item.fundName}}</td>
                  <td v-if="item.actualSize==''||item.actualSize==null">无</td>
                  <td v-else>{{item.actualSize}}</td>
                  <td>{{item.name}}</td>
                  <td>{{item.com_count}}</td>
                  <td v-if="item.regTime==''||item.regTime==null">无</td>
                  <td v-else>{{item.regTime}}</td>
                  <td>
                    <!-- <span class="table-link" @click="goEditorInfo(item.id,item.fundName)">编辑</span> -->
                    <span class="table-link" @click="toTransferManage(item.id,item.fundName)">分配管理人</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  fundManageList,
  downloadExcel
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      currentPage: 1,
      total: null,
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表

    }
  },
  created() {
    let page = 1;
  	if (this.$route.query.page) {
  		page = parseInt(this.$route.query.page);
  	}
    this.currentPage = page;

    this.initData();
  },
  computed: {},
  mounted() {},
  methods: {
    initData() {
      this.datalist = [];
      fundManageList({
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist = res.data.payload.data;
          this.total = res.data.payload.total;
        }
      })
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val;
        this.initData();
      }
    },
    //去修改
    goEditorInfo(id, name) {
      this.$router.push({
        path: '/home/fundManageDetail',
        query: {
          id: id,
          name: encodeURIComponent(name)
        }
      })
    },
    //去新建一行
    goCreatnew() {
      this.$router.push({
        path: '/home/fundManageDetail',
      })
    },
    downloadFundFile() {
      downloadExcel().then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
    //
    toTransferManage(id, name) {
      this.$router.push({
        path: '/home/fundManageToSet',
        query: {
          id: id,
          page:this.currentPage,
          name: encodeURIComponent(name)
        }
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  line-height: 30px;
  font-size: 14px;
  padding: 0px 10px;
  margin-right: 32px;
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
    float: left;
    margin-top: 5px;
    margin-right: 5px;
  }
  .icon_add {
    background: url('../../images/projectpermissions_icon.png') no-repeat;
    background-size: 100%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    margin-top: 4px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

</style>



